import { openModal, closeModal } from "./modal";
import { postData } from "../services/services";

function forms(formSelector, modalTimerId) {
  // Forms
  const forms = document.querySelectorAll(formSelector);

  const message = {
    loading: "img/form/spinner.svg",
    success: "!     ",
    failure: "-   ...",
  };

  //      postData
  forms.forEach((item) => {
    bindPostData(item);
  });
  

  //     
  function bindPostData(form) {
    //     ,  button   submit
    form.addEventListener("submit", (e) => {
      // preventDefault -    
      e.preventDefault();

      //      
      const statusMessage = document.createElement("img");
      //   ,  2 :
      // statusMessage.setAttribute('src', message.loading);
      statusMessage.src = message.loading;
      statusMessage.classList.add("form_image");
      // form.append(statusMessage);
      // insertAdjacentElement -  append,    
      form.insertAdjacentElement("afterend", statusMessage);

      //      
      const formData = new FormData(form);

      //   FormData  JSON 
      const json = JSON.stringify(Object.fromEntries(formData.entries()));
      // fromEntries -    
      // entries -    
      // JSON.stringify -      JSON

      postData("http://localhost:3000/requests", json)
        .then((data) => {
          console.log(data);
          //     
          showThanksModal(message.success, true);
        })
        .catch(() => {
          showThanksModal(message.failure, false);
        })
        .finally(() => {
          //     - reset
          form.reset();
          //   
          statusMessage.remove();
        });
    });
  }

  //     
  function showThanksModal(message, complete) {
    //     ,    
    const modalDialog = document.querySelector(".modal__dialog");
    modalDialog.classList.add("hide");
    //    ,       
    openModal('.modal', modalTimerId);

    //      
    const thanksModal = document.createElement("div");
    thanksModal.classList.add("modal__dialog");
    thanksModal.innerHTML = `
            <div class="modal__content">
                <div class="modal__close" data-close>&times;</div>
                <div class="modal__title">${message}</div>
                <div class="progress_bar">
                    <div class="my_progress_bar"></div>
                </div>
            </div>
        `;

    //      
    modal.append(thanksModal);

    // Progress bar
    const myProgressBar = document.querySelector(".my_progress_bar");
    setTimeout(() => {
      if (complete) {
        myProgressBar.style.width = "100%";
      } else {
        myProgressBar.style.width = "37%";
        myProgressBar.style.backgroundColor = "red";
      }
    }, 100);

    //   ,   
    setTimeout(() => {
      thanksModal.remove();
      modalDialog.classList.add("show");
      modalDialog.classList.remove("hide");
      closeModal(".modal");
    }, 3000);
  }

  //   json-server
  fetch("http://localhost:3000/menu") // 
    .then((data) => data.json()) //      json 
    .then((res) => {
      //    
      console.log(res);
    });
}

export default forms;
